@import url(https://fonts.googleapis.com/css2?family=Rubik&display=swap);
/* ===================================== 
  BASE STYLES
======================================== */
* {
  box-sizing: border-box;
  font-family: 'Rubik';
}

body {
  background-color: mintcream;
}

/* FONTS */

.main-header,
.container,
footer {
  width: 95%;
  margin: 0 auto;
}

.intro,
.desc,
.project-info .info {
  text-align: left;
}

.header,
.quotes,
blockquote,
.main-nav .nav-btn,
.project-title,
/* .project-desc, */
.project-desc li,
.resume {
  text-align: center;
}

.intro,
.desc,
.info {
  line-height: 1.4;
  padding-left: 5px;
}

.main-nav {
  margin-top: 0;
}

.main-nav li,
.card li,
.project-card li {
  list-style: none;
}

/* links to sections of the site and external links */
.main-nav a,
.svg-container a,
.project-card a,
.link,
.certification,
.repo-btn,
.bp-pts a {
  text-decoration: none;
}

.main-nav a,
.svg-container a {
  display: block;
}

.main-nav,
.project-desc,
.svg-container {
  padding-left: 0;
}

/* navigation links */
.main-nav .nav-btn,
.links-container a {
  font-size: 1rem;
  color: #fff;
  background-color: #3385ff;
  text-align: center;
  display: block;
  padding: 0.6rem;
  transition: 0.3s;
}

.main-nav .active {
  background-color: #f2f2ed;
  color: #3b80d1;
}

/* containers */
.wrapper {
  min-height: calc(100vh - 312px);
}

/* home */
.card {
  padding-bottom: 0.4rem;
}

.repo-btn,
.links-container a {
  border-radius: 0.6rem;
}

.repo-btn {
  font-size: 1rem;
  color: #fff;
  background-color: #3385ff;
  display: block;
  border-radius: 0.6rem;
  margin: 0 auto;
  padding: 1rem;
  width: 30%;
  text-align: center;
}

/* about page */
blockquote,
.statement {
  font-family: fantasy;
}

blockquote::before {
  content: open-quote;
}

.card,
.project-card {
  box-shadow: 0 5px 10px darkgrey;
}

blockquote::after {
  content: close-quote;
}

/* projects page */
.project-desc,
.links-container,
.svg-container {
  display: flex;
}

.project-desc {
  flex-direction: column;
  justify-content: space-evenly;
}

.links-container {
  justify-content: space-around;
  padding-bottom: 0.5rem;
}

/* =========RESUME========= */
.skills-container .bp-pts {
  list-style-position: inside;
  display: table;
  padding-left: 0;
  margin: 0 auto;
}

.skills-container h2 {
  text-align: center;
}

.bp-pts {
  line-height: 1.8;
}

.bold li:first-child {
  font-weight: 900;
  font-size: 1.2rem;
}

/* =========FOOTER & SVGs========= */
.svg-container {
  margin: 0 auto;
  flex-direction: column;
  align-items: center;
}

.svg-container li {
  list-style: none;
  margin: 5px 0;
}

footer svg {
  display: block;
  width: 2.5em;
  height: 2.5em;
  margin: 5px auto;
}

@-webkit-keyframes offset {
  100% {
    stroke-dashoffset: 0;
  }
}

@keyframes offset {
  100% {
    stroke-dashoffset: 0;
  }
}

@-webkit-keyframes fill-it {
  100% {
    fill: #8a2be2;
  }
}

@keyframes fill-it {
  100% {
    fill: #8a2be2;
  }
}

@-webkit-keyframes fill-in {
  100% {
    fill: #3385ff;
  }
}

@keyframes fill-in {
  100% {
    fill: #3385ff;
  }
}

@-webkit-keyframes colored {
  100% {
    fill: #ff1a1a;
  }
}

@keyframes colored {
  100% {
    fill: #ff1a1a;
  }
}

@-webkit-keyframes color-in {
  100% {
    fill: #33cc33;
  }
}

@keyframes color-in {
  100% {
    fill: #33cc33;
  }
}

.github,
.gmail,
.linkedIn,
.phone {
  stroke-width: 0.2;
}

.github {
  stroke: #8a2be2;
  stroke-dasharray: 139;
  stroke-dashoffset: 139;
  -webkit-animation: offset 4s linear forwards, fill-it 4s 4s forwards;
          animation: offset 4s linear forwards, fill-it 4s 4s forwards;
}

.gmail {
  stroke: #ff1a1a;
  stroke-dasharray: 117;
  stroke-dashoffset: 117;
  -webkit-animation: offset 4s linear forwards, colored 4s 4s forwards;
          animation: offset 4s linear forwards, colored 4s 4s forwards;
}

.linkedIn {
  stroke: #87cefa;
  stroke-dasharray: 197;
  stroke-dashoffset: 197;
  -webkit-animation: offset 8s linear forwards, fill-in 4s 4s forwards;
          animation: offset 8s linear forwards, fill-in 4s 4s forwards;
}

.phone {
  stroke: #33cc33;
  stroke-dasharray: 66;
  stroke-dashoffset: 66;
  -webkit-animation: offset 5s linear forwards, color-in 4s 4s forwards;
          animation: offset 5s linear forwards, color-in 4s 4s forwards;
}

.project-desc a,
footer a:visited,
.link,
.certification,
.bp-pts a {
  color: #0456ef;
}

.links-container a {
  color: #fff;
}

/* =========PROJECTS PAGE========= */
.img,
.project-img,
.card-img {
  display: block;
  height: 200px;
  width: 200px;
  margin: 0 auto;
}

