.self {
  background-image: url(/reactprofile/static/media/self.da1c9e54.jpeg);
}
.vg {
  background-image: url(data:image/jpeg;base64,);
}
.providers {
  background-image: url(/reactprofile/static/media/providers.d672de7e.png);
}
.threebd {
  background-image: url(/reactprofile/static/media/3bd.1ef2dbf3.jpg);
}
.pagination {
  background-image: url(/reactprofile/static/media/pagination.542da698.jpeg);
}
.feld {
  background-image: url(/reactprofile/static/media/feld.4187bbab.jpg);
}

.rsvp {
  background-image: url(/reactprofile/static/media/rsvp.c8aba6e2.jpeg);
}
.gallery {
  background-image: url(/reactprofile/static/media/gallery.66dae124.jpeg);
}

/* ===================================== 
  MEDIA QUERIES
======================================== */
@media (min-width: 768px) {
  .main-nav {
    display: flex;
  }

  .main-nav li {
    flex-grow: 1;
  }

  .skills-container .bp-pts {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }

  .main-header,
  .container,
  footer {
    width: 80%;
  }

  /* ====ABOUT PAGE & PROJECTS PAGE==== */
  .card-img {
    float: left;
    margin-right: 7px;
  }

  .home .card {
    display: block;
    position: relative;
  }

  .repo-btn {
    position: absolute;
    right: 20%;
  }

  .card,
  .project-card {
    box-shadow: 0 5px 10px darkgrey;
  }

  .clearfix::after {
    content: '';
    clear: both;
    display: table;
  }

  .project-title {
    margin: 0;
  }
}

@media (min-width: 1024px) {
  .wrapper {
    min-height: calc(100vh - 78px);
  }

  .card,
  .project-card {
    max-width: 700px;
    margin: 20px auto;
  }

  .main-header,
  .container,
  footer {
    width: 75%;
    max-width: 1150px;
  }

  .svg-container {
    flex-direction: row;
    justify-content: space-around;
  }
}

